.combinerContainer {
  display: flex;
  flex-wrap: wrap;
  margin: 1rem auto;
  justify-content: center;
  background-color: $color-liquid;
  border-radius: 50%;
  border: 30px solid $color-cauldron;
  width: 30rem;
  height: 30rem;
  transition: all 1s ease;

  &.disabled {
    pointer-events: none;
    opacity: 70%;
  }
}
